* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
  }
  
  html {
    /* background: #faf7f2; */
    background: #faf7f2;
    background-image: url(https://s3.postimg.org/s1n3ji1ur/paper_fibers_2_X.png);
    box-sizing: border-box;
    font-family: "Lato", sans-serif;
    font-size: 10px;
    font-weight: 400;
  }
  
  *,
  *:before,
  *:after {
    box-sizing: inherit;
  }
  
  .subtle {
    color: #aaa;
  }
  
  .main-card--container {
    display: grid;
    grid-template-columns: repeat(3, minmax(30rem, 50rem));
    grid-gap: 4rem;
    justify-content: space-around;
    height: auto;
    padding: 2rem;
  }
  
  .card-container {
    margin: 2.5rem auto 0;
    position: relative;
    display: grid;
    grid-template-columns: 1fr;
    justify-content: space-around;
    align-items: flex-start;
    margin: 2.4rem;
    box-shadow: 10px 10px 10px 5px rgba(0, 0,0, 0.1);
  }
  
  .card-container img {
    max-width: auto;
    height: 30rem;
  }
  
  .card {
    background-color: #fff;
    padding: 3rem;
    position: relative;
    box-shadow: 0 0 0.5rem rgba(75, 75, 75, 0.07);
    z-index: 1;
    box-sizing: border-box;
  }
  
  .card-body {
    display: inline-block;
  }
  
  .card-number {
    margin-top: 1.5rem;
  }
  
  .card-circle {
    border: 0.1rem solid #aaa;
    border-radius: 50%;
    display: inline-block;
    line-height: 2.2rem;
    font-size: 1.2rem;
    height: 2.5rem;
    text-align: center;
    width: 2.5rem;
  }
  
  .card-author {
    display: block;
    font-size: 1.2rem;
    letter-spacing: 0.5px;
    margin: 1.5rem 0 0;
    text-transform: uppercase;
  }
  
  .card-title {
    font-family: "Cormorant Garamond", serif;
    font-size: 6rem;
    font-weight: 300;
    line-height: 6rem;
    margin: 1rem 0;
    text-transform: capitalize;
  }
  
  .card-description {
    /* display: inline-block; */
    font-weight: 300;
    line-height: 2.2rem;
    margin: 1rem 0;
    font-size: 1.4rem;
  }
  
  .card-read {
    cursor: pointer;
    font-size: 1.4rem;
    font-weight: 700;
    letter-spacing: 0.6rem;
    margin: 0.5rem 0 2rem;
    position: relative;
    text-align: right;
    text-transform: uppercase;
  }
  
  .card-read:after {
    background-color: #b8bddd;
    content: "";
    display: block;
    height: 0.1rem;
    position: absolute;
    top: 0.9rem;
    width: 75%;
  }
  
  .card-tag {
    float: right;
    margin: 5px 0 0;
    border: 1px solid #aaa;
    padding: 5px 10px;
    cursor: pointer;
  }
  
  .card-tag:hover {
    background: #aaa;
    color: black;
  }
  
  .card-media {
    max-width: 100%;
    height: auto;
  }
  
  .card-shadow {
    background-color: #fff;
    box-shadow: 0 2px 25px 2px rgba(0, 0, 0, 1), 0 2px 50px 2px rgba(0, 0, 0, 1),
      0 0 100px 3px rgba(0, 0, 0, 0.25);
    height: 1px;
    margin: -1px auto 0;
    width: 80%;
    z-index: -1;
  }
  
  @media (max-width: 998px) {
    .main-card--cointainer {
      display: grid;
      grid-template-columns: repeat(1, minmax(20rem, 50rem));
      grid-gap: 5rem;
      justify-content: space-around;
    }
  }
  
  /* ---------- Button styles ------------------ */
  
  .navbar {
    padding-top: 5rem;
    text-align: center;
    
  }
  .navbar h1 {
    padding-bottom: 2rem;
    text-transform: capitalize;
  }
  
  /** button group styles **/
  .btn-group {
    border-radius: 1rem;
    text-transform: capitalize;
  }
  .btn-group__item {
    border: none;
    /* min-width: 6rem; */
    padding: 1.5rem 3rem;
    background-color: rgb(23, 22, 22);
    color: white;
    cursor: pointer;
    margin: 0;
    font-size: 1.7rem;
    box-shadow: inset 0px 0px 0px -1.5rem rebeccapurple;
    transition: all 300ms ease-out;
    text-transform: capitalize;
  }
  .btn-group__item:last-child {
    border-top-right-radius: 1rem;
    border-bottom-right-radius: 1rem;
  }
  .btn-group__item:first-child {
    border-top-left-radius: 1rem;
    border-bottom-left-radius: 1rem;
  }
  .btn-group__item:hover,
  .btn-group__item:focus {
    color: rebeccapurple;
    box-shadow: inset 0px -2rem 0px -1.5rem rebeccapurple;
  }
  .btn-group__item:focus {
    outline: none;
  }
  .btn-group__item:after {
    content: "✔️";
    margin-left: 0.5rem;
    display: inline-block;
    color: rebeccapurple;
    position: absolute;
    transform: translatey(1rem);
    opacity: 0;
    transition: all 200ms ease-out;
  }
  .btn-group__item--active:after {
    opacity: 1;
    transform: translatey(-0.2rem);
  }